export default {
  name: "verify",
  data() {
    return {
      user: undefined,
      token: undefined,
      send_loading: false,
      send_disable: false,
      send_label: "发送验证邮件"
    }
  },
  created() {
    window._t = this;
    this.user = this.$store.state.user;
    this.token = this.$route.params.token;
  },
  methods: {
    on_send_event: function () {
      this.send_loading = true;
      var post_data = {
        'action': 'resend_email'
      };
      this.$axios.post('/app/user/info', post_data).then((response) => {
        console.log("response:", response);
        this.send_disable = true;
        this.send_loading = false;
        this.$messageX("已发送邮件，请前往邮箱验证！");
        this.send_label = "已发送邮件，请前往邮箱验证！"
      }).catch(() => {
        this.$messageX('网络问题，请重新尝试！');
        this.send_loading = false;
      });
    },

  }
}
